import React from "react"
import styled from "styled-components"
import ProfileImage from "../../../assets/self-picture.png"
import { AndroidLogo } from "../../common/svgs"

function Landing() {
    return (
        <Container>
            <img
                data-aos="fade-up"
                data-aos-duration="1500"
                className="profile-image"
                src={ProfileImage}
                alt="Moi avec les bras croisés"
                loading="eager"
            />
            <div className="container-text">
                <h3 className="text-name">David Dekeuwer</h3>

                <h2 className="text-developer">
                    Développeur <span>d'application</span>
                </h2>

                <div className="container-android">
                    <h2 className="text-android">Android</h2>
                    <AndroidLogo fill="#fff" className="logo-android" />
                </div>
            </div>
        </Container>
    )
}

const Container = styled.section`
    display: flex;
    align-self: center;
    width: 85%;
    max-width: 1100px;
    min-height: 100vh;
    align-items: center;
    flex-wrap: wrap;
    justify-content: center;

    .container-text {
        flex: 1 0 0px;
        display: flex;
        flex-direction: column;
        margin-top: 10%;
        color: white;
        align-self: center;
        text-align: start;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        font-size: 14px;
    }

    .text-name {
        margin-bottom: -0.3em;
        font-style: normal;
        font-size: 2.2em;
        font-weight: lighter;
    }

    .text-developer {
        font-size: 3em;
        font-weight: normal;
    }

    .container-android {
        display: inline-flex;
        align-items: center;
        margin-top: 0.3em;
        align-self: flex-end;
        font-size: 2.7em;

        .text-android {
            font-weight: normal;
        }

        .logo-android {
            * {
                transition: all 0.5s ease;
            }

            &:hover * {
                transition: all 0.5s ease;
                fill: #3ddb85;
            }
            margin-left: 0.5em;
        }
    }

    .profile-image {
        flex: 1 1 0;
        object-fit: cover;
        max-width: 550px;
        min-width: 400px;
    }

    @media (max-width: 1030px) {
        .container-text {
            font-size: 13px;
        }
    }

    @media (max-width: 534px) {
        .text-developer span {
            margin-left: 3em;
        }
    }
`

export default Landing
